.landing-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  height: 100vh;
  justify-content: center;
  background: linear-gradient(rgba(13, 110, 253, 0.9), rgba(13, 110, 253, 0.9)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.landing-page {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.landing-wrapper .title,
.title-description {
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
  font-size: 48px;
  text-transform: uppercase;
  color: #fff;
  text-shadow: -0.01em 0.01em 0.1em black;
}

.landing-wrapper .title-description {
  color: #eee;
  font-size: 16px;
  text-transform: lowercase;
}

#navbar-container {
  background-color: #0d6efd;
  letter-spacing: 3px;
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
  text-transform: uppercase;
}

.nav-link {
  color: #fff !important;
}

.nav-link.active {
  color: #00c853 !important;
}

.navbar-brand {
  margin-right: auto !important;
}

.navbar-toggler {
  background-color: #00c853 !important;
}

.contact-info-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #00c853;
  padding: 85px 20px;
}

.contact-title-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Viga", sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  color: #fff;
  text-shadow: -0.01em 0.01em 0.1em black;
  padding-bottom: 40px;
}

.contact-title {
  font-size: 40px;
}

.contact-title-subtext {
  font-size: 16px;
}

.profile-pic-wrapper {
  padding-bottom: 40px;
}

.profile-pic {
  border-radius: 50%;
  max-height: 150px;
  max-width: 100%;
}

.info-icons {
  display: flex;
  flex-direction: row;
  width: 200px;
  justify-content: space-between;
}

.info-icon {
  border-radius: 50%;
  max-height: 30px;
  width: 30px;
}

.about-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  justify-content: center;
}

.about-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 150px 100px;
  font-family: "Fira Mono", monospace;
  height: 100vh;
  border-bottom: 2px solid #0d6efd;
  background: linear-gradient(rgba(0, 0, 0, 0.98), rgba(0, 0, 0, 0.98)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.about {
  display: flex;
  color: white;
}

.about-info {
  text-align: center;
  max-height: 100vh;
  padding-left: 100px;
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.about-info-text {
  opacity: 0.8;
  font-size: 1.2em;
  line-height: 40px;
  opacity: 0.8;
}

.about-img {
  width: 300px;
  height: 600px;
  object-fit: cover;
  border-radius: 20px;
}

.timeline {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 85px 20px;
  border-bottom: 1px solid #0d6efd;
  background-color: black;
  font-family: "Cuprum", sans-serif;
}

.timeline-header {
  padding-bottom: 40px;
  color: white;
  font-family: "Fira Mono", monospace;
}

.timeline-items {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.timeline-item {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  width: 300px;
  height: 200px;
  border-radius: 20%;
  margin-bottom: 20px;
}

.timeline-item:nth-child(even) {
  margin-left: 38%;
}

.timeline-item:nth-child(odd) {
  margin-right: 38%;
}

.memory-image {
  position: absolute;
  left: 45%;
  width: 120px;
  height: 120px;
}

.image-container {
  display: block;
  width: 100%;
  height: 100%;
}

.image-style {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  opacity: 1;
  border-radius: 50%;
  z-index: 1;
}

.timeline-item::before {
  left: calc(50% - 10px);
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: #0d6efd;
  margin-top: 150px;
}

.timeline-item:nth-child(1) {
  background-color: #24a5dc;
}

.timeline-item:nth-child(2) {
  background-color: #df802e;
}

.timeline-item:nth-child(3) {
  background-color: #25d13c;
}

.timeline-item:nth-child(4) {
  background-color: #f04419;
}

.timeline-item:nth-child(5) {
  background-color: #a624e7;
}

.timeline-item:nth-child(6) {
  background-color: #d6d019;
}

.final-image-wrapper {
  margin: 40px;
}

.tile {
  padding: 0 40px;
}

.tile-grow {
  transition: all 0.4s ease-in-out;
}

.tile-grow:hover {
  transform: scale(1.2);
}

.tech-stack {
  position: relative;
  background-color: #734ae8;
  background-image: linear-gradient(315deg, #734ae8 0%, #89d4cf 74%);
  border-bottom: 2px solid #0d6efd;
  height: 600px;
  padding: 85px 200px;
  color: white;
  font-family: "Fira Mono", monospace;
  display: grid;
  grid-template-rows: [header-start] 1fr [description-start] 1fr [icon-start] 2fr [footer-start] 2fr;
  grid-template-columns: [icon1] 1fr [icon2] 1fr [icon3] 1fr [icon4] 1fr;
  grid-template-areas: "header header header header" "description description description description" "icon1 icon2 icon3 icon4" "footer footer footer footer";
  align-items: center;
  justify-content: center;
  text-align: center;
}

.tech-stack-header {
  grid-area: header;
  opacity: 1;
}

.description {
  opacity: 0.8;
  grid-area: description;
  margin-bottom: 60px;
}

.python {
  grid-area: icon1;
}

.flask {
  grid-area: icon2;
}

.react {
  grid-area: icon3;
}

.javascript {
  grid-area: icon4;
}

.icon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tech-stack-title {
  font-size: 3rem;
}

.icon-title {
  opacity: 0.8;
  margin-bottom: 10px;
  font-size: 2rem;
}

.icon-img-container {
  width: 100px;
  height: 100px;
}

.icon-img {
  width: 100%;
  height: 100%;
}

.blog-page-wrapper {
  display: flex;
  justify-content: center;
  color: #00c853;
  min-height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.98), rgba(0, 0, 0, 0.98)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.blog-main-title-wrapper {
  font-family: "Architects Daughter", cursive;
  font-weight: bold;
  color: #0d6efd;
  text-align: center;
  text-transform: uppercase;
}

.blog-main-page {
  display: flex;
  flex-direction: column;
  max-width: 1260px;
  padding: 85px 40px;
}

.blog-articles-info-wrapper {
  display: flex;
  flex-direction: column;
}

.blog-article-box-wrapper {
  display: flex;
  margin-bottom: 30px;
  padding: 20px 15px;
  font-family: "Cuprum", sans-serif;
  border-bottom: 1px solid #0d6efd;
}

.blog-article-box-wrapper:hover {
  box-shadow: 0 0 11px #ffad05;
}

.blog-article-image-wrapper {
  width: 250px;
  height: 200px;
  margin-right: 30px;
}

.blog-article-image {
  width: 100%;
  height: 100%;
}

.blog-article-title {
  font-family: "Cuprum", sans-serif;
  text-transform: uppercase;
  font-size: 1.5rem;
}

.blog-article-description {
  font-size: 1.15rem;
}

.blog-info-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.blog-articles-wrapper {
  display: flex;
}

.side-panel {
  margin-left: 40px;
  width: 200px;
  height: calc(100% - 30px);
  background: linear-gradient(rgba(13, 110, 253, 0.9), rgba(13, 110, 253, 0.9)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}